'use client'
import { useUser } from '@clerk/nextjs'
import React from 'react'
import  UseProject  from '@/hooks/use-project'

const DashboardPage = () => {
  const {user} =  useUser()
  const {project} = UseProject();
  return (
    <div>
      {project?.name}
    </div>
  )
}

export default DashboardPage